@import url('https://fonts.googleapis.com/css2?family=Koulen&display=swap'); /*'Koulen', arial */
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap'); /*'Roboto Condensed', sans-serif */
@import url('https://fonts.googleapis.com/css2?family=Kanit&display=swap'); /* 'Kanit', sans-serif */
@import url('https://fonts.googleapis.com/css2?family=Signika+Negative:wght@500&display=swap'); /* 'Signika Negative', sans-serif */

.profileContainer {
  display: flex;
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0%;
  left: 0%;
  max-width: 100%;
  background-color: white;
}

.profileContainer > div {
  display: flex;
  height: 100vh;
  width: 100vw;
  max-width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.profileContainer > div:first-child > h1 {
  color: rgb(7, 1, 124);
  font: 500 2.5vmax "Rampart One", arial;
  transform: translateX(-0.4vmax) translateY(-2vmax);
}

.profileContainer > div:first-child > img {
  width: 20vmax;
  border-radius: 50%;
  transition: all 0.5s;
  padding: 15px;  
  border: 2px dashed rgba(30, 28, 34, 0.527);
}

.profileContainer > div:first-child > img:hover {
  transform: scale(1.05);
}

.profileContainer > div:first-child > a {
  border: none;
  background-color: #7f5a83;
  background-image: linear-gradient(315deg, #7f5a83 0%, #0d324d 74%);
  font: 400 1.2vmax 'Roboto Condensed', sans-serif;
  color: white;
  text-decoration: none;
  padding: 0.7vmax;
  width: 30%;
  margin: 4vmax;
  text-align: center;
  transition: all 0.5s;
}

.profileContainer > div:first-child > a:hover {
  background-color: rgb(204, 78, 56);
  transform: translateX(10px);
}

.profileContainer > div:last-child {
  justify-content: space-evenly;
  align-items: flex-start;
  padding: 5vmax;
  box-sizing: border-box;
}

.profileContainer > div:last-child > div > h4 {
  color: black;
  font: 400 1.5vmax 'Roboto Condensed', sans-serif;
}

.profileContainer > div:last-child > div > p {
  color: rgba(29, 29, 29, 0.518);
  font: 400 1.3vmax 'Signika Negative', sans-serif;
  margin: 0.2vmax;
}

.profileContainer > div:last-child > div:last-child {
  display: flex;
  flex-direction: column;
  width: 60%;
}

.profileContainer > div:last-child > div:last-child > a {
  border: none;
  background-color: #7f5a83;
  background-image: linear-gradient(315deg, #7f5a83 0%, #0d324d 74%);
  font: 400 1.3vmax 'Roboto Condensed', sans-serif;
  color: white;
  text-decoration: none;
  padding: 0.6vmax;
  text-align: center;
  transition: all 0.5s;
  margin: 1vmax 0;
}

.profileContainer > div:last-child > div:last-child > a:hover {
  background-color: rgb(31, 31, 31);
  border-radius: 20px 0 20px 0;
  transform: scale(1.1);
}

@media screen and (max-width: 600px) {
  .profileContainer {
    flex-direction: column;
  }

  .profileContainer > div:first-child > h1 {
    font: 500 3.2vmax "Roboto";
    transform: translateY(-2vmax);
  }

  .profileContainer > div:first-child > a {
    font: 400 1.7vmax "Roboto";
    padding: 1vmax;
  }

  .profileContainer > div:last-child {
    text-align: center;
    align-items: center;
  }

  .profileContainer > div:last-child > div > h4 {
    font: 400 2.5vmax "Roboto";
  }

  .profileContainer > div:last-child > div > p {
    font: 400 2vmax arial;
    margin: 0.5vmax 0;
  }

  .profileContainer > div:last-child > div:last-child > a {
    font: 400 1.8vmax "Roboto";
    padding: 1vmax;
    margin: 2vmax 0;
  }
}